import { Log } from '@microsoft/sp-core-library';
import { BaseApplicationCustomizer } from '@microsoft/sp-application-base';

const LOG_SOURCE: string = 'HubNavigationApplicationCustomizer';

/**
 * Application Customizer that highlights ONLY the current site in hub navigation
 * All other menu items remain with normal font weight
 */
export default class HubNavigationApplicationCustomizer
  extends BaseApplicationCustomizer<{}> {
  
  private styleElement: HTMLStyleElement | null = null;
  private currentSiteUrl: string = '';
  private currentSiteName: string = '';
  private observer: MutationObserver | null = null;

  public onInit(): Promise<void> {
    Log.info(LOG_SOURCE, 'Initialized HubNavigationApplicationCustomizer');

    // Get current site URL and extract site name
    this.currentSiteUrl = this.context.pageContext.web.absoluteUrl.toLowerCase();
    this.currentSiteName = this.extractSiteName(this.currentSiteUrl);
    
    console.log('[HubNav] ====================================');
    console.log('[HubNav] Current Site URL:', this.currentSiteUrl);
    console.log('[HubNav] Current Site Name:', this.currentSiteName);
    console.log('[HubNav] ====================================');

    // Inject styles
    this.injectStyles();

    // Initial highlight
    this.highlightCurrentSite();

    // Watch for DOM changes (mega menu opening)
    this.setupObserver();

    // Listen for navigation
    this.context.application.navigatedEvent.add(this, () => {
      this.highlightCurrentSite();
    });

    return Promise.resolve();
  }

  protected onDispose(): void {
    if (this.observer) {
      this.observer.disconnect();
    }
    this.removeStyles();
  }

  /**
   * Extract site name from URL (e.g., "dev-anil-standard-communication")
   */
  private extractSiteName(url: string): string {
    const match = url.match(/\/(sites|teams)\/([^\/\?#]+)/i);
    return match ? match[2].toLowerCase() : '';
  }

  /**
   * Setup mutation observer
   */
  private setupObserver(): void {
    this.observer = new MutationObserver(() => {
      this.highlightCurrentSite();
    });

    this.observer.observe(document.body, {
      childList: true,
      subtree: true
    });
  }

  /**
   * Highlight current site
   */
  private highlightCurrentSite(): void {
    [100, 300, 600, 1000, 2000].forEach(delay => {
      setTimeout(() => this.applyHighlight(), delay);
    });
  }

  /**
   * Apply highlight to ONLY the current site link
   */
  private applyHighlight(): void {
    // First, clear ALL previous highlights
    document.querySelectorAll('[data-current-site="true"]').forEach(el => {
      el.removeAttribute('data-current-site');
    });

    // Find all anchor links
    const allLinks = document.querySelectorAll('a[href]');
    
    allLinks.forEach((link: Element) => {
      const anchor = link as HTMLAnchorElement;
      const href = (anchor.href || '').toLowerCase();
      const linkSiteName = this.extractSiteName(href);
      
      // STRICT match - only exact site name
      if (linkSiteName && linkSiteName === this.currentSiteName) {
        const linkText = (anchor.textContent || '').trim();
        console.log('[HubNav] â˜… MATCH - Making BOLD:', linkText);
        
        // Mark this element and its children
        anchor.setAttribute('data-current-site', 'true');
        
        // Mark parent li
        const parentLi = anchor.closest('li');
        if (parentLi) {
          parentLi.setAttribute('data-current-site', 'true');
        }
      }
    });
  }

  /**
   * Inject CSS styles using data attribute selector
   */
  private injectStyles(): void {
    if (this.styleElement) return;

    const styles = `
      /* ==============================================
         CURRENT SITE ONLY = BOLD
         Everything else = NORMAL (browser default)
         ============================================== */
      
      /* Target ONLY elements with data-current-site attribute */
      [data-current-site="true"],
      [data-current-site="true"] span,
      [data-current-site="true"] > a,
      [data-current-site="true"] > a span,
      li[data-current-site="true"] > a,
      li[data-current-site="true"] > a span,
      a[data-current-site="true"],
      a[data-current-site="true"] span {
        font-weight: 700 !important;
      }

      /* Highlight background for dropdown item */
      li[data-current-site="true"] {
        background-color: #e8f4fc !important;
        border-left: 4px solid #0078d4 !important;
      }

      /* Text color for current site */
      li[data-current-site="true"] > a,
      li[data-current-site="true"] > a span,
      a[data-current-site="true"],
      a[data-current-site="true"] span {
        color: #0078d4 !important;
      }
    `;

    this.styleElement = document.createElement('style');
    this.styleElement.id = 'hub-nav-highlight';
    this.styleElement.textContent = styles;
    document.head.appendChild(this.styleElement);
    
    console.log('[HubNav] Styles injected');
  }

  /**
   * Remove styles and attributes
   */
  private removeStyles(): void {
    if (this.styleElement && this.styleElement.parentNode) {
      this.styleElement.parentNode.removeChild(this.styleElement);
      this.styleElement = null;
    }
    
    document.querySelectorAll('[data-current-site]').forEach(el => {
      el.removeAttribute('data-current-site');
    });
  }
}
